
.Form label{ 
    display: block;
    padding: 0.5em;
}
.Form input[type="text"]{
    width: 100%;
}
.Form input[type="submit"]{
    display: block;
    margin: auto;
    padding:2px 10px;
    border: 1px solid grey;
    border-radius: 5px;
    background-color:whitesmoke;
    color: black;
    text-decoration:none;
    cursor: pointer;

}
.Form input[type="submit"]:hover{
    background-color:#E5E5E5;
}
.Form textarea {
    resize: vertical;
    width: 100%;
    height: 200px;
}
.Form{
    grid-area: Form;
}
