.RightClick ul{
    position: fixed;
    z-index: 1;
    width: 150px;
    list-style: none;
    margin: 0;
    padding:3px 0;
    user-select: none;
    background-color: white;
    direction: ltr;
    text-align: right;

    border: 0.5px solid rgb(220 220 220);
    box-shadow: 
    2px 2px 2px 0px rgb(0 0 0 / 41%);
    font-size: small;display: none;

}

.RightClick li{
    padding: 5px 20px;
    margin: 0;
    background-color: white;

}
.RightClick hr{
    border-top:1px solid rgb(220 220 220);
    border-bottom:none;

    margin:3px 0;  
}
.RightClick li:hover,.RightClick li:active{
    background-color: rgb(0 0 0 / 10%);
}
