.Sidebar {
	position: relative;
    min-height: 200px;
    direction: ltr;
}
.Sidebar>.button{
    display: block;
}
.Keyboard{
    position: absolute;
    top: 0px;
    right: 10px;
}
.Sidebar .hr{
    margin-top: 20px;
    height: 173px;
}
.Sidebar .currentChord{
    position: relative;
    text-align: center;
    font-size: xx-large;
    font-weight: bold;
    direction: ltr;
    box-shadow: inset 0px 0px 2px 2px rgb(18 111 212 / 67%);
    padding: 3px 0;
    margin-bottom:20px;
    margin-top:10px;
}
.Sidebar .currentChord::before{
    text-align: center;
    font-size: medium;
    font-weight: normal;
    direction: rtl;
    position: absolute;
    top: -20px;
    right: 0px;
    content: 'אקורד נוכחי:';
}
.Sidebar button,.Sidebar input[type='button']{
	margin: 2px 2px;
    padding:2px 10px;
    border: 1px solid grey;
    border-radius: 5px;
    background-color:whitesmoke;
    color: black;
    text-decoration:none;
    cursor: pointer;
    white-space: pre;
}
.Sidebar>button.current{
background-color: #ccc;
}
