.Letter{
    position: relative;
    display: inline-block;

    border: none;
    background-color: #0000;
    padding: 0;
    margin: 0;

    font-size: large;
    cursor: pointer;
}
/*etter>.chord:active {
    cursor: -webkit-grabbing;
    cursor: grabbing;
}*/
.Letter>.chord{
    position: absolute;
    
    border: none;
    background-color: #0000;
    padding: 0;
    margin: 0;

    z-index: 1;
    bottom: 100%;
    right: 50%;
    margin-right: -4px;
    margin-bottom: -7.5px;

    white-space: pre;
    font-size: large;
    cursor: pointer;
    font-weight: 475;
    direction: ltr;

    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.Letter:hover{
    background-color: antiquewhite;
}
.Letter:hover>.chord{
    background-color: antiquewhite;
    border-top: 1px solid grey;
}
.Letter>.chord:hover{
    background-color: antiquewhite;
    border-top: 1px solid grey;

}

