.Keyboard {
    transform: scale(.7);
    cursor: pointer;
}

.Keyboard i{
    font-style: normal;
    margin-bottom: 5px;
    font-size: 21px;
}
.Keyboard .current{
    background-color: #ccc;
    color: black;
}
.C,.D,.E,.F,.G,.A,.B{
    position: absolute;
    z-index: 0;
    height: 200px;
    width: 48px;
    background-color: white;
    border: #777777 solid 2px;
    border-radius: 10px;
    /**/
    display: grid;
    justify-items: center;
    align-items: end;
}
.Db,.Eb,.Gb,.Ab,.Bb{
    position: absolute;
    z-index: 1;
    height: 123px;
    width: 38px;
    background-color: black;
    border: black solid 2px;
    border-radius: 10px;
    /**/
    color: white;
    direction: ltr;
    display: grid;
    justify-items: center;
    align-items: end;    
    transform: scaleX(0.9);
}
.C{
    right: 300px;
}
.D{
    right: 250px;
}
.E{
    right: 200px;
}
.F{
    right: 150px;
}
.G{
    right: 100px;
}
.A{
    right: 50px;
}
.B{
    right: 0px;
}

.Db{
    right: 280px;
}
.Eb{
    right: 230px;
}
.Gb{
    right: 130px;
}
.Ab{
    right: 80px;
}
.Bb{
    right: 30px;
}

