.Main{
    grid-area: main;
}
aside{
    grid-area: sidebar;
}

.Footer{
    grid-area: footer;
}
.Edit{
    grid-area: edit;
}
.Head{
    grid-area: head;
    transform: translateY(1em);
}
.Form{
    grid-area: Form;
}
.Main,.Edit, aside, .Header, .Footer, .Head, .Form{
    /*border: 1px solid #AAA;*/
    box-shadow: 
    0px 0px 1px 0 rgb(0 0 0 / 40%); 
    /*1px 4px 3px 0 rgb(0 0 0 / 20%), 
    1px 4px 7px 0 rgb(0 0 0 / 19%);*/
    background-color: white;
    padding: 1em;

}
.Page{
    display: grid;
    grid-template-areas: 
    'head head head'
    'Form Form Form'
    'sidebar main edit'
    'footer footer footer'
    ;
    background-color: whitesmoke;
    grid-template-rows:  auto auto auto auto;
	grid-template-columns:300px 1fr 250px;
    grid-gap: 1em;
    padding: 1em 5em;
    margin: auto;
    
}
body{
    background-color: whitesmoke;
}
@media screen and (max-width:1250px) {
    .Page{
        padding: 1em 2em;
    }
}
@media screen and (max-width:1100px) {
    .Page{
        display: grid;
        grid-template-areas: 
        'head head'
        'Form Form'
        'sidebar main'
        'edit main'
        'footer footer'
        ;
        background-color: #f1f1f1;
        grid-template-rows:  auto auto 1fr 1fr auto;
        grid-template-columns:300px 1fr;
        }
        aside{
            /*border: 0.5px solid #AAA;*/
            border-radius:  0;
            background-color: white;
        }

        
}
@media screen and (max-width:850px) {
    .Page{
        display: grid;
        grid-template-areas: 
        'head'
        'Form'
        'main'
        'sidebar'
        'edit'
        'footer'
        ;
        grid-template-rows: auto;
        grid-template-columns:auto;
    
        background-color: #f1f1f1;
        grid-gap: 1em;
        padding: 1em;
    }
}
@media print{
    .Edit, aside, .Header, .Footer, .Head{
        display: none;
    }
    .Page{
        display: block;
        background-color: white;
    }  
    .Page::after{
        /*content: "אתר לכתיבת אקורדים - http://localhost:3000";*/
        margin-right: 30px;
    }
    html *{
        background-color: white;
    }
    .Main{
        box-shadow: none;
    
    }
    .Head{
        display: none;
    }
    .Letter:hover{
            background-color: revert;
    }
    .Letter:hover>.chord{
            background-color: revert;
            border-top: none;
    }
    .Letter>.chord:hover{
            background-color: revert;
            border-top: none;
        
    } 
}